import React from "react";
import Common from "./Common"
import web from "../src/images/growth.svg";
import Footer from "./Footer"
const Home = () => {
  return (
    <>
        <Common
        name="Grow your business with"
        imgsrc={web}
        visit="/contact"
        btname="Get Started"
      />
       <Footer />
    </>
  );
};
export default Home;
