import React, { useState } from "react";
import ListCom from "./ListCom";
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
const ToDoList = () => {
  const [item, updateitem] = useState("");
  const [newitem, newupdateditem] = useState([]);

  const InputEvent = (event) => {
    updateitem(event.target.value);
  };
  const clickEvent = () => {
    newupdateditem((preValue) => {
      return[...preValue, item]
    });
    updateitem(" ");
  };

  return (
    <>
    <div className="main_div">
        <div className="center_div">
            <br />
            <h1 className="todoheading">ToDO List</h1>
            <br />
            <input
        type="text"
        placeholder="Add items"
        value={item}
        onChange={InputEvent}
      />
      <Button onClick={clickEvent} className="newBtn"><AddIcon /></Button>
      <br />
      {newitem.map((curElem, index) => {
        return (
          <ListCom key={index} text={curElem}/>
        );
      })}
        </div>
    </div>
    </>
  );
};
export default ToDoList;
