import React from "react";
import Card2 from "./Card2";
import Sdata2 from "./Sdata2";

const Netflix = () => {
  return (
    <>
      <div className="my-5">
        <h1 className="text-center text-capitalize">all netflix series</h1>
        </div>
        <div className="container-fluid mb-5">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row gy-5">
                {Sdata2.map((curElem, index) => {
                  return (
                    <Card2
                      key={index}
                      sname={curElem.sname}
                      imgscr={curElem.imgscr}
                      title={curElem.title}
                      link={curElem.link}
                    />
                  );
                })}
              </div>
            </div>
          </div>
        </div>
    </>
  );
};
export default Netflix;
